header {
  .carousel {
    .carousel-indicators {
      li {
        width: 14px;
        height: 14px;
        border-radius: 50%;
        background: #fff;
        &.active {
          background: #8546a7;
          border: 3px solid #fff;
        }
      }
    }
    .carousel-inner {
      .carousel-item {
        .info {
          position: absolute;
          width: 100%;
          display: flex;
          margin: auto;
          justify-content: space-around;
          .lang {
            left: 2%;
            a {
              .md(font-size, 10px);
              padding: 3px 5px;
              text-decoration: none;
              font-size: 12px;
              background: #fff;
              border-radius: 50%;
              color: #9f9f9f;
              &:hover {
                background: #8546a7;
                color: #fff;
                &.active {
                  background: #fff;
                  color: #9f9f9f;
                }
              }
              &.active {
                background: #8546a7;
                color: #fff;
              }
            }
          }
          .addres {
            right: 2%;
            flex-direction: column;
            display: flex;
            text-align: -moz-right;
            .lg(flex-direction, row);
            a {
              font-size: 14px;
              .lg(font-size, 10px);
              padding: 2px 5px;
              text-decoration: none;
              background: #fff;
              color: #8546a7;
              display: table;
              .fa {
                margin-right: 4px;
              }
              &:hover {
                background: #8546a7;
                color: #fff;
                &.active {
                  background: #fff;
                  color: #8546a7;
                }
              }
              &.active {
                color: #fff;
                background: #8546a7;
              }
            }
          }
        }
        .carousel-caption {
          font-family: 'HelveticaNeueCyr';
          text-transform: uppercase;
          justify-content: space-evenly;
          img {
            width: 25%;
          }
          .title {
            align-self: center;
            text-align: left;
            h6 {
              font-size: 12px;
              font-weight: 900;
              background: #5bb962;
              display: inline-block;
              padding: 5px;
              margin: 0;
              .lg(font-size, 10px);
            }
            p {
              font-size: 29px;
              font-weight: 900;
              background: #838f9a;
              opacity: .9;
              display: block;
              padding: 0 10px;
              .lg(font-size, 24px);
            }
            .btn {
              font-size: 12px;
              background: #8546a7;
              color: #fff;
              display: block;
              .lg(font-size, 10px);
            }
          }
        }
      }
    }
  }
.navbar{
  
  .navbar-brand{
  width: 33px;
  height: 33px;
  color: #8546a7;
  border: #ced4da 1px solid;
  text-align: center;
  line-height: 1.3;
  .md(margin, 0);
  }
  
  .collapse{
    position: relative;
    .media-block({
      display: block;
      align-self: baseline;
      flex-basis: auto;
    },991px);
    .navbar-nav{
      position: absolute;
      width: 75%;
      justify-content: space-between;
      .media-block({
        flex-direction: row;
      },991px);
      .md-block({
    flex-direction: column;
    top: 32px;
    width: 100%;
    display: none;
    z-index: 999;
      });
      .nav-item{
      
        .nav-link{
          .md-block({
    text-align: center;
    color: #fff;
    background: #8546a7;
    &:hover{
      color: #fff;
      background: #572c6e;
    }
          });
          color: #646464;
          &:hover{
            color: #8546a7;
          }
          .xl(font-size,14px);
          .lg(font-size,12px);
          &::after{
            display: none;
          }
          .fa{
            margin-left: .255em;
            padding: .5px 1.5px;
            border-radius: 50%;
            &:hover{
              background: #8546a7;
              color: #fff;
            }
          }
        }
        
          .dropdown-menu{
            background: #572c6e;
            border: none;
            padding: 0;
            .dropdown-item{
              color: #fff;
              padding:12px 8px;
              &:hover{
                background: #8546a7;
              }
            }
           
            .media-block({
             position: absolute !important;
             font-size: 14px;
               },991px);
        }
      }
    }
    .form-inline{
      .media-block({
        align-items: baseline ;
      },991px);
      position: relative;
      width: 100%;
      .form-control{
        width: 81%;
        border-radius: 0;
        position: absolute;
        height: 33px;
        display: none;
        .md-block({
          display: block !important;
          width: 92%;
        });
        &::placeholder{
         text-transform: uppercase;
          color: #8546a7;
          font-size: 14px;
          .xl(font-size,12px);
          .media-block({
            font-size: 10px;
             },991px);
        }
        &:focus{
       box-shadow: none;
       border-color: #ced4da;;
        }
      }
      button{
        height: 33px;
        border-radius: 0;
        position: absolute;
          background: #8546a7;
          color: #fff;
          border: none;
          &:hover{
            background: #572c6e;
          }
      &.search{
          right: 16% ;
          z-index: 5;
          .md-block({
          right: 0;
          });
        }
       &.comment{
          right: 0% ;
          z-index: 5;
          .xl(font-size,12px);
          .media-block({
         right: -4%;
         font-size: 10px;
          },991px);
          .md-block({
            top: 270px;
            width: 100%;
            display: none; 
            right: 0;        
          });
        }
      }
      
    }
  }
}
}


 