#slider{
    background: #f9f3de;
    margin-top: 50px;
    padding: 30px;
    .slider {
      .slick-arrow{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E");
        &.slick-prev{
            background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E");
        }
     
        &::before{
          display: none;
        }
      }
      display: flex;
        .item {
          display: flex;
          justify-content: space-between;
          width: 45%;
          .md(flex-direction,column);
          .img {
            width: 14%;
            margin: 10px 0;
            .md-block({
                width: 30%;
                align-self: center;        
            });
            img {}
          }
          .text {
              .md(width,100%);
            width: 84%;
            display: flex;
            flex-direction: column;
            span {
              font-family: 'Open Sans';
              font-size: 13px;
              font-weight: lighter;
              color: #3a3a3a;
              display: inline;
              margin: 10px 0;
            }
            h3 {
              color: #8546a7;
              font-size: 20px;
              font-family: 'HelveticaNeueCyr';
              font-weight: lighter;
              margin: 10px 0;
              display: inline;
            }
            p {
              font-family: 'Open Sans';
              font-size: 15px;
              font-weight: lighter;
              color: #3a3a3a;
              display: inline;
              margin: 10px 0;
              height: 130px;
           .lg(height, 180px);
           .md(height, auto);
            }
            .name {
              font-family: 'Open Sans';
              font-size: 15px;
              font-weight: lighter;
              color: #3a3a3a;
              display: inline;
              margin-top: auto;
            }
          }
        }
      }  
}

