.footer{
    background: url('../img/footerBg.png');
    padding: 40px 0;
    .title{
        margin-bottom: 35px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      p{
          font-family: 'Open Sans';
          font-size: 16px;
          font-weight: lighter;
          color: #fff;
          max-width: 550px;
          align-self: center;
      }  
    }
    h3{
        font-family: 'HelveticaNeueCyr';
        color: #fff;
        font-weight: lighter;
        font-size: 20px;
    }
    .navbar-nav{
        margin-bottom: 20px;
        .btn{
            align-self: flex-start;
            border-radius: 0;
              background: #8546a7;
              color: #fff;
              border: none;
        }
        li, a{
            display: flex;
            .fa{
                margin-right: 12px;
            }
            font-family: 'Open Sans';
            font-size: 14px;
            font-weight: lighter;
            color: #fff;
            
        }
    }
    img{
        margin-bottom: 20px;
    }
}