.sidebar{
    padding: 10px 0;
    h3{
        font-size: 26px;
        font-family: 'HelveticaNeueCyr';
        font-weight: lighter;
        color: #8546a7;
        margin-bottom: 25px;
        margin-top: 40px;
        .xl(font-size, 24px);
    }
    .widget_text{
        &.last{
            display: none;
            .lg(display, block);
        }
        span{
            color: #62ae67;
            font-size: 13px;
            font-family: 'Open Sans';
            font-weight: lighter;
            margin-bottom: 18px;
        }
        .widget-title{
            font-family: 'HelveticaNeueCyr';
            font-size: 20px;
            color: #828282;
            font-weight: lighter;
            .xl(font-size, 16px);
        }
        .textwidget{
            &.bor {
                border-bottom: 1px solid #828282;
                margin-bottom: 15px;
                .lg-block({
                    border-bottom:   none;
                    margin-bottom: 0;
                })
            }
            font-family: 'Open Sans';
            font-size: 15px;
            color: #828282;
            font-weight: lighter;
            padding-bottom: 15px;
            .xl(font-size, 12px);
        }
        a{
            color: #8546a7;
            font-size: 12px;
            font-family: 'Open Sans';
            text-transform: uppercase;
            .lg(display, none);
        }
    }
    
    .carousel{
      position: relative;
      .carousel-indicators{
          position: absolute;
          z-index: 5;
          top: 110%;
          right: 75%;
          margin-right: 0;
          margin-left: 0;
          li {
            width: 14px;
            height: 14px;
            border-radius: 50%;
            background: #fff;
            border: 1px solid #e6e6e6;
            &.active {
              background-color: #8546a7;
              border: 1px solid #e6e6e6;
            }
          }
      }  
    }
    
}