.elite{
   padding: 50px 0;
   position: relative;
   .img1{
       position: absolute;
      right: 0;
      top: 30%;
      width: 10%;
   }
   .img2{
       position: absolute;
      left: 0;
      top: 30%;
      width: 10%;
   }
    .elite-magic-tour{
        font-family: 'Open Sans'; 
        margin-bottom: 50px; 
    h3{
     color:#8546a7;
     font-size: 32px;
     font-weight: 300;   
    }   
    p{
        color:#828282;
        font-family: 'Open Sans';
        .xl(font-size,14px);
    } 
    a{
        color: #8546a7;
        font-size: 12px;
        text-align: left;
        text-transform: uppercase;
    }
    }
    .podberi-svoy-tur{
        background: url('../img/elite-bg.png') center center no-repeat;
        background-size: cover;
        padding: 30px;
        margin-bottom: 50px; 
     
        h3{
            color: #fff;
            font-size: 23px;
        }
        input{
            border-radius: 0;
            width: 45%;
            &.strana{
                width: 100%;
            }
            &::placeholder{
                font-size: 15px;
                color: #828282;
                font-family: 'Open Sans';
                font-weight: 300; 
            }
        }
        label{
            font-size: 14px;
            color: #fff;
            font-family: 'Open Sans';
            font-weight: 300;
        }
        .btn{
            background: #8546a7;
            color: #fff;
           border-radius: 0;
            &:hover{
              background: #572c6e;
            }
        }
    }
}