.curorts{
    position: relative;
    padding: 50px 0;
    h2{
        
    }
    .card{
        border: 1px solid #ededed;
        padding: 5px;
        margin: 15px 0;
        .card-img-top{
            margin-bottom: 10px;
        }
        .card-body{
            border: none;
            padding: 5px;
            .card-title{
                font-family: 'HelveticaNeueCyr';
                font-size: 20px;
                font-weight: 300; 
                color: #828282;
            }
            .card-subtitle{
                font-family: 'Open Sans';
                font-size: 15px;
                font-weight: lighter;
                color: #62ae67 !important;
            }
            .card-text{
                .fa{
                    color: #62ae67;
                }
                color: #3a3a3a;
                font-family: 'Open Sans';
                font-weight: lighter;
                font-size: 13px;
            }
            .btn{
              color: #3a3a3a;
              font-size: 15px;  
              border-radius: 0;
              background: #f5df00;
              font-family: 'Open Sans';
              border: none;
              .xl(font-size, 14px);
            }
            .card-link{
                color: #8546a7;
                font-size: 18px;
                font-family: 'HelveticaNeueCyr';
                font-weight: lighter;
                .xl(font-size, 14px);
            }
        }
    }
    .all{
        font-size: 14px;
        color: #fff;
        font-family: 'Open Sans';
        font-weight: lighter;
        background: #8546a7;
        border: none;
        border-radius: 0;
        padding: 10px 15px;
        position: absolute;
        z-index: 5;
        right: 0;
    }
}