.four{
    background: #f9f3de;
  
    .box{
        padding: 38px 0;
        background: transparent;
        position: relative;
      text-align: center;
      .first{
      opacity: 1;
      transition: 1.5s;
      &:hover{
            opacity: 0;
    }
        h3{
            margin-top: 35px;
            color:#8546a7;
            font-size: 26px;
            font-family: 'HelveticaNeueCyr';
            font-weight: 300;
            text-transform: capitalize;
        }  
      }
       
        .last{
            &:hover{
                    opacity: 1;
            }
            transition: 2s;
            opacity: 0;
            top: 0;
            height: 100%;
            
            z-index: 100;
            position: absolute;
            text-align: center;
           padding: 35px 10px;
            background: #8546a7 ;
            h3{
                color: #f2eacc;
                font-size: 26px;
                font-family: 'HelveticaNeueCyr';
                font-weight: bold;
                text-transform: capitalize;
            }
            p{
                font-family: 'Open Sans';
                color: #fff;
                font-size: 15px;
                font-weight: lighter;
            }
        }
    }
}